"use client"

import { PublicNav } from "@/components/public-nav"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Sprout, Calendar, MapPin, CheckCircle } from "lucide-react"
import { useState, useEffect } from "react"
import { toast } from "sonner"

export default function FarmerPortalPage() {
  const [requestedItems, setRequestedItems] = useState<string[]>([])
  const [availableOrganic, setAvailableOrganic] = useState<any[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [selectedItem, setSelectedItem] = useState<any>(null)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [preferredDate, setPreferredDate] = useState("")
  const [preferredTime, setPreferredTime] = useState("")
  const [notes, setNotes] = useState("")
  const [myRequests, setMyRequests] = useState<any[]>([])
  const [showMyRequests, setShowMyRequests] = useState(false)

  useEffect(() => {
    const fetchAvailableOrganic = async () => {
      try {
        setIsLoading(true)
        console.log("Fetching available organic waste from /api/farmer/available")
        const response = await fetch("/api/farmer/available")
        console.log("Response status:", response.status)
        
        if (response.ok) {
          const data = await response.json()
          console.log("Received data:", data)
          
          // Transform API data to match display format
          const transformed = data.map((item: any) => {
            // Parse expiry date - handle both ISO strings and date strings
            let expiryDate = "N/A"
            if (item.expiryDate) {
              try {
                // If it's already a date string (YYYY-MM-DD), use it directly
                if (item.expiryDate.match(/^\d{4}-\d{2}-\d{2}$/)) {
                  expiryDate = item.expiryDate
                } else {
                  // Otherwise parse it
                  expiryDate = new Date(item.expiryDate).toISOString().split("T")[0]
                }
              } catch (e) {
                expiryDate = item.expiryDate
              }
            }
            
            return {
              id: item.id,
              cafe: item.cafeName || "Green Café",
              item: item.itemName,
              quantity: `${item.quantity} kg`,
              collectionDate: expiryDate,
              location: "123 Main St, Downtown", // Default location
              distance: "2.3 km", // Default distance
            }
          })
          console.log("Transformed data:", transformed)
          setAvailableOrganic(transformed)
        } else {
          const errorText = await response.text()
          console.error("Failed to fetch available organic waste:", response.status, errorText)
        }
      } catch (error) {
        console.error("Error fetching available organic waste:", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchAvailableOrganic()
    // Refresh every 5 seconds to show new items
    const interval = setInterval(fetchAvailableOrganic, 5000)
    return () => clearInterval(interval)
  }, [])

  // Fetch user's requests
  useEffect(() => {
    const fetchMyRequests = async () => {
      try {
        const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
        const requesterId = storedUser.userId || "u-farmer-1"
        
        const response = await fetch("/api/farmer/requests", {
          headers: {
            "x-user-id": requesterId,
          },
        })
        
        if (response.ok) {
          const data = await response.json()
          // API now includes waste details in the response
          setMyRequests(data)
        }
      } catch (error) {
        console.error("Error fetching my requests:", error)
      }
    }

    fetchMyRequests()
    // Refresh every 5 seconds
    const interval = setInterval(fetchMyRequests, 5000)
    return () => clearInterval(interval)
  }, [])

  const handleRequestClick = (item: any) => {
    setSelectedItem(item)
    setIsDialogOpen(true)
  }

  const handleSubmitRequest = async () => {
    if (!selectedItem) return

    if (!preferredDate || !preferredTime) {
      toast.error("Please select a preferred date and time")
      return
    }

    try {
      // Get user from localStorage (if available) or use a default farmer user
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      const requesterId = storedUser.userId || "u-farmer-1" // Default farmer user for demo

      const preferredTimeString = `${preferredDate} ${preferredTime}`

      const response = await fetch("/api/farmer/requests", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": requesterId,
        },
        body: JSON.stringify({
          wasteId: selectedItem.id,
          requesterId: requesterId,
          preferredTime: preferredTimeString,
          notes: notes || undefined,
        }),
      })

      if (response.ok) {
        toast.success("Pickup request submitted successfully", {
          description: `Your request for "${selectedItem.item}" has been sent. The café will review and coordinate the collection time with you.`,
          duration: 6000,
          position: "top-center",
        })
        // Update the specific item in the list to show "Pending Approval" status
        // DO NOT refresh the list - keep all items visible
        setAvailableOrganic((prev) =>
          prev.map((item) =>
            item.id === selectedItem.id
              ? { ...item, requestStatus: "pending" }
              : item
          )
        )
        
        setRequestedItems([...requestedItems, selectedItem.id])
        setIsDialogOpen(false)
        setPreferredDate("")
        setPreferredTime("")
        setNotes("")
        setSelectedItem(null)
        // DO NOT refresh available items - this would remove the item from the list
      } else {
        const errorData = await response.json()
        toast.error("❌ Request Failed", {
          description: errorData.error || "Failed to send collection request. Please try again.",
          duration: 6000,
          position: "top-center",
        })
      }
    } catch (error) {
      console.error("Error submitting request:", error)
      toast.error("❌ Error", {
        description: "Something went wrong. Please try again.",
        duration: 6000,
        position: "top-center",
      })
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <PublicNav />

      <main className="pt-24 px-4 pb-12">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-12">
            <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-secondary/10 mb-4">
              <Sprout className="w-8 h-8 text-secondary" />
            </div>
            <h1 className="font-display font-bold text-4xl text-foreground mb-3">Farmer Portal</h1>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Browse available organic waste and coffee grounds from local cafés for composting and farming
            </p>
          </div>

          <Card className="p-6 mb-8 bg-green-50 border-green-200">
            <h3 className="font-display font-bold text-lg text-green-900 mb-2">How It Works</h3>
            <ol className="space-y-2 text-sm text-green-800">
              <li>1. Browse organic waste and coffee grounds available below</li>
              <li>2. Click "Request Collection" for items you need</li>
              <li>3. Wait for café admin approval (usually within 24 hours)</li>
              <li>4. Once approved, coordinate collection time with the café</li>
            </ol>
          </Card>

          <div className="mb-6 flex items-center justify-between">
            <div>
              <h2 className="font-display font-bold text-2xl text-foreground mb-4">
                {showMyRequests ? "My Collection Requests" : "Available Organic Waste"}
              </h2>
              <p className="text-muted-foreground">
                {showMyRequests
                  ? `${myRequests.length} request${myRequests.length !== 1 ? "s" : ""}`
                  : isLoading
                    ? "Loading..."
                    : `${availableOrganic.length} item${availableOrganic.length !== 1 ? "s" : ""} available for collection`}
              </p>
            </div>
            <Button
              variant={showMyRequests ? "default" : "outline"}
              onClick={() => setShowMyRequests(!showMyRequests)}
            >
              {showMyRequests ? "Browse Available Waste" : "View My Requests"}
            </Button>
          </div>

          {showMyRequests ? (
            // My Requests View
            myRequests.length === 0 ? (
              <Card className="p-6">
                <p className="text-center text-muted-foreground">You haven't made any collection requests yet.</p>
              </Card>
            ) : (
              <div className="grid gap-6">
                {myRequests.map((request) => (
                  <Card key={request.id} className="p-6 hover:shadow-lg transition-shadow">
                    <div className="flex items-start justify-between gap-6">
                      <div className="flex-1">
                        <div className="flex items-center gap-3 mb-3">
                          <div className="w-12 h-12 rounded-full bg-secondary/10 flex items-center justify-center flex-shrink-0">
                            <Sprout className="w-6 h-6 text-secondary" />
                          </div>
                          <div>
                            <h3 className="font-display font-bold text-xl text-foreground">{request.itemName || "Unknown Item"}</h3>
                            <p className="text-sm text-muted-foreground">{request.cafeName || "Green Café"}</p>
                          </div>
                        </div>

                        <div className="grid md:grid-cols-2 gap-4 mb-4">
                          <div className="flex items-center gap-2 text-muted-foreground">
                            <span className="text-sm">
                              Quantity: <span className="font-medium text-foreground">{request.quantity || 0} kg</span>
                            </span>
                          </div>
                          <div className="flex items-center gap-2 text-muted-foreground">
                            <Calendar className="w-4 h-4" />
                            <span className="text-sm">
                              Requested: <span className="font-medium text-foreground">
                                {new Date(request.requestedAt).toLocaleDateString()}
                              </span>
                            </span>
                          </div>
                        </div>

                        {request.preferredTime && (
                          <div className="mb-3 p-3 bg-green-50 rounded-lg border border-green-200">
                            <p className="text-sm font-medium text-green-900 mb-1">Preferred Collection Time:</p>
                            <p className="text-sm text-green-800">{request.preferredTime}</p>
                          </div>
                        )}

                        {request.notes && (
                          <div className="mb-3">
                            <p className="text-sm text-muted-foreground">Notes: {request.notes}</p>
                          </div>
                        )}

                        <div className="mt-4">
                          <span
                            className={`inline-flex items-center gap-1 px-3 py-1 rounded-full text-xs font-medium ${
                              request.status === "approved"
                                ? "bg-green-500/10 text-green-600 border border-green-500/20"
                                : request.status === "rejected"
                                  ? "bg-red-500/10 text-red-600 border border-red-500/20"
                                  : request.status === "collected"
                                    ? "bg-primary/10 text-primary border border-primary/20"
                                    : "bg-amber-500/10 text-amber-600 border border-amber-500/20"
                            }`}
                          >
                            {request.status === "approved" && <CheckCircle className="w-3 h-3" />}
                            <span className="capitalize">{request.status}</span>
                          </span>
                        </div>

                        {request.status === "approved" && (
                          <div className="mt-4 p-3 bg-green-50 rounded-lg border border-green-200">
                            <p className="text-sm font-medium text-green-900 mb-1">✅ Request Approved!</p>
                            <p className="text-sm text-green-800">
                              Please coordinate the final collection time with the café. Your preferred time was: {request.preferredTime || "Not specified"}
                            </p>
                          </div>
                        )}
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            )
          ) : isLoading ? (
            <Card className="p-6">
              <p className="text-center text-muted-foreground">Loading available organic waste...</p>
            </Card>
          ) : availableOrganic.length === 0 ? (
            <Card className="p-6">
              <p className="text-center text-muted-foreground">No organic waste items available at the moment. Check back later!</p>
            </Card>
          ) : (
            <div className="grid gap-6">
              {availableOrganic.map((item) => (
              <Card key={item.id} className="p-6 hover:shadow-lg transition-shadow">
                <div className="flex items-start justify-between gap-6">
                  <div className="flex-1">
                    <div className="flex items-center gap-3 mb-3">
                      <div className="w-12 h-12 rounded-full bg-secondary/10 flex items-center justify-center flex-shrink-0">
                        <Sprout className="w-6 h-6 text-secondary" />
                      </div>
                      <div>
                        <h3 className="font-display font-bold text-xl text-foreground">{item.item}</h3>
                        <p className="text-sm text-muted-foreground">{item.cafe}</p>
                      </div>
                    </div>

                    <div className="grid md:grid-cols-2 gap-4 mb-4">
                      <div className="flex items-center gap-2 text-muted-foreground">
                        <Calendar className="w-4 h-4" />
                        <span className="text-sm">
                          Available: <span className="font-medium text-foreground">{item.collectionDate}</span>
                        </span>
                      </div>
                      <div className="flex items-center gap-2 text-muted-foreground">
                        <MapPin className="w-4 h-4" />
                        <span className="text-sm">
                          <span className="font-medium text-foreground">{item.distance}</span> away
                        </span>
                      </div>
                    </div>

                    <div className="flex items-center gap-2 mb-4">
                      <span className="text-sm text-muted-foreground">Quantity:</span>
                      <span className="font-bold text-lg text-foreground">{item.quantity}</span>
                    </div>

                    <p className="text-sm text-muted-foreground">{item.location}</p>
                  </div>

                  <div className="flex flex-col items-end gap-3">
                    {item.requestStatus === "pending" || requestedItems.includes(item.id) ? (
                      <Button disabled className="bg-gray-400 text-white cursor-not-allowed">
                        <CheckCircle className="w-4 h-4 mr-2" />
                        Pending Approval
                      </Button>
                    ) : (
                      <Button
                        onClick={() => handleRequestClick(item)}
                        className="bg-secondary hover:bg-secondary/90 text-white"
                      >
                        <Sprout className="w-4 h-4 mr-2" />
                        Request Collection
                      </Button>
                    )}
                    <Button variant="outline" size="sm">
                      View Details
                    </Button>
                  </div>
                </div>
              </Card>
              ))}
            </div>
          )}

          {/* Collection Request Dialog */}
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogContent className="sm:max-w-[500px]">
              <DialogHeader>
                <DialogTitle>Request Collection</DialogTitle>
                <DialogDescription>
                  Coordinate a collection time for {selectedItem?.item} from {selectedItem?.cafe}
                </DialogDescription>
              </DialogHeader>
              <div className="space-y-4 py-4">
                <div className="space-y-2">
                  <Label htmlFor="preferred-date">Preferred Collection Date *</Label>
                  <Input
                    id="preferred-date"
                    type="date"
                    value={preferredDate}
                    onChange={(e) => setPreferredDate(e.target.value)}
                    min={new Date().toISOString().split("T")[0]}
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="preferred-time">Preferred Collection Time *</Label>
                  <Select value={preferredTime} onValueChange={setPreferredTime}>
                    <SelectTrigger id="preferred-time">
                      <SelectValue placeholder="Select time" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="Morning (9:00 AM - 12:00 PM)">Morning (9:00 AM - 12:00 PM)</SelectItem>
                      <SelectItem value="Afternoon (12:00 PM - 5:00 PM)">Afternoon (12:00 PM - 5:00 PM)</SelectItem>
                      <SelectItem value="Evening (5:00 PM - 8:00 PM)">Evening (5:00 PM - 8:00 PM)</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="notes">Additional Notes (Optional)</Label>
                  <Textarea
                    id="notes"
                    placeholder="Any special instructions or requirements..."
                    value={notes}
                    onChange={(e) => setNotes(e.target.value)}
                    rows={3}
                  />
                </div>
              </div>
              <DialogFooter>
                <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
                  Cancel
                </Button>
                <Button onClick={handleSubmitRequest} className="bg-secondary hover:bg-secondary/90 text-white">
                  <Sprout className="w-4 h-4 mr-2" />
                  Send Request
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>
      </main>
    </div>
  )
}
